import React from 'react';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import ParkingTab from '../screens/ParkingTab'; // AsegÃºrate de que la ruta sea correcta

const Tab = createBottomTabNavigator();

const AppNavigator = () => {
  return (
    <Tab.Navigator>
      <Tab.Screen name="Parking 1" children={() => <ParkingTab tabName="Parking 1" />} />
      <Tab.Screen name="Parking 2" children={() => <ParkingTab tabName="Parking 2" />} />
    </Tab.Navigator>
  );
};

export default AppNavigator;
