import React, { useState, useEffect } from 'react';
import { View, Text, StyleSheet } from 'react-native';
import mqtt from 'mqtt';

const ParkingTab = ({ tabName }) => {
  const [slots, setSlots] = useState([false, false, false]); // Estado inicial de los slots

  useEffect(() => {
    const client = mqtt.connect('ws://test.mosquitto.org:8080');

    client.on('connect', function () {
      console.log('Conectado a MQTT');
      client.subscribe('parking/sensors', function (err) {
        if (err) {
          console.error('Error al suscribirse:', err);
        }
      });
    });


    client.on('message', function (topic, message) {
      const messageStr = message.toString();
      console.log('Mensaje MQTT:', messageStr);
      const allSlotsStatus = messageStr.split(',').map(status => status === '255'); // '255' es ocupado

      let slotsToShow;
      if (tabName === 'Parking 1') {
        slotsToShow = allSlotsStatus.slice(0, 3);
      } else {
        slotsToShow = allSlotsStatus.slice(3, 6);
      }

      setSlots(prevSlots => {
        const updatedSlots = prevSlots.map((slot, index) => {
          // Actualiza solo si el estado actual es diferente al estado recibido
          if (slot !== slotsToShow[index]) {
            return slotsToShow[index];
          }
          return slot;
        });

        return updatedSlots;
      });
    });

    return () => {
      if (client) {
        client.end();
      }
    };
  }, [tabName]);

  return (
    <View style={styles.container}>
      <Text style={styles.title}>{tabName}</Text>
      {slots.map((occupied, index) => (
        <View key={index} style={[styles.led, occupied ? styles.ledOn : styles.ledOff]} />
      ))}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  title: {
    fontSize: 24,
    marginBottom: 20,
  },
  led: {
    width: 50,
    height: 50,
    borderRadius: 25,
    margin: 10,
  },
  ledOn: {
    backgroundColor: 'red',
  },
  ledOff: {
    backgroundColor: 'green',
  },
});

export default ParkingTab;